* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  background-image: url("data:image/svg+xml;utf8,<svg xmlns=\"http://www.w3.org/2000/svg\" width=\"25\" height=\"25\"><circle r=\"2\" fill=\"%23888\" /></svg>");
  background-position: 10px 10px;
  background-repeat: repeat;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin: 16px;
  padding: 8px;
}

.country-list {
  margin: 0 16px;
  padding: 0;
  list-style: none;
}

.country-list__item {
  width: fit-content;
  background-color: #fdfdfd;
  border: 1px solid #2a2a2a;
  border-radius: 3px;
  align-items: center;
  padding: 0 10px;
  font-size: 23px;
  font-weight: 500;
  display: flex;
}

.country-list__item:not(:last-child) {
  margin-bottom: 10px;
}

.country-list__img {
  margin-right: 10px;
}

.country-info {
  margin-left: 16px;
}

.card {
  width: 500px;
  background-color: #fdfdfd;
  border: 1px solid #2a2a2a;
  border-radius: 3px;
  padding: 10px;
}

.card__box {
  align-items: center;
  display: flex;
}

.card__img {
  margin-right: 10px;
}

.card__text {
  font-size: 35px;
  font-weight: 600;
}

.list {
  padding: 0;
  list-style: none;
}

.list__text {
  font-size: 18px;
  font-weight: 700;
}

.list__subtext {
  font-weight: 400;
}

